@mixin cache-image($hauteur, $zIndex) {
  content: "";
  position: absolute;
  width: 100%;
  height: $hauteur;
  background-color: white;
  opacity: 0;
  z-index: $zIndex;
}

@mixin mobile {
  @media screen and (max-width: map-get($breakpoint, mobile)) {
    @content;
  }
}

@mixin tablette {
  @media screen and (max-width: map-get($breakpoint, tablette)) {
    @content;
  }
}

@mixin laptop {
  @media screen and (max-width: map-get($breakpoint, laptop)) {
    @content;
  }
}

@mixin mediaAvecSurvol($hover) {
  @media (hover: $hover) {
    @content;
  }
}
